import ajax from './ajax'


export const ReqTypes = () => ajax("http://47.112.132.177:8092/form/find/all",{},'POST')  //
export const RewYearTypeEnable = (fileEnable,fileType,pageNum,pageSize) => ajax("http://47.112.132.177:8092/file/find/yte",{fileEnable,fileType,pageNum,pageSize},'POST')  //
export const RewTypeEnable = (fileEnable,pageNum,pageSize) => ajax("http://47.112.132.177:8092/file/find/yte",{fileEnable,pageNum,pageSize},'POST')  //
export const ChangeEnable = (fileEnable,id) => ajax("http://47.112.132.177:8092/file/update/enable",{fileEnable,id},'POST')  //
export const login = (username,password) => ajax("http://47.112.132.177:8092/login",{username,password},'POST')  //
export const ReqType = (id) => ajax("http://47.112.132.177:8092/form/find/id",{id},'POST')  //ID
export const Download = (id) => ajax("http://47.112.132.177:8092/file/download?id=",{id},'GET')  //ID




